import React from 'react';
import Link from 'gatsby-link';
import ebayIndiaBanner from "./static/images/ebayIndiaBanner.png";
import ebayEmailer1 from "./static/images/ebayEmailer1.jpg";
import ebayEmailer3 from "./static/images/ebayEmailer3.jpg";
import ebayEmailer2 from "./static/images/ebayEmailer2.jpg";
import ebayEmailer4 from "./static/images/ebayEmailer4.jpg";
import volkswagenEmailerImg from "./static/images/volkswagenEmailerImg.jpg";
import radioShowImg from "./static/images/radioShowImg.jpg";

const ebayindia = () => (
  <div className="fullBox">
    <div className="insideBanner ebayIndiaBanner">
      <div className="bg ebayIndia"></div>
      <div className="midWrapper">
            <div className="rightSection">
              <div className="box">
                <div className="vMid">
                  <img src={ebayIndiaBanner} alt="" />
                </div>
              </div>
            </div>
            <div className="leftSection">
              <div className="box">
                <div className="vMid">
                  <h1><span style={{textTransform:'lowercase'}}>e</span>Bay India</h1>
                  <h2><span>NEWSLETTERS</span></h2>
                  <p>Client : Ebay India<br />Work : Emailers<br />Agency : 22feet Tribal Worldwide</p>
                </div>
              </div>
            </div>
      </div>
    </div>

    <div id="insideContent" className="ebayIndiaContent">
      <div className="midWrapper">
        <div className="row">
          <ul className="two">
            <li><img src={ebayEmailer1} alt="" /> <img src={ebayEmailer3} alt="" /></li>
            <li><img src={ebayEmailer2} alt="" /> <img src={ebayEmailer4} alt="" className="lastImg" /></li>
          </ul>
        </div>
      </div>
    </div>

    <div className="midWrapper nextPrevious textAlignCenter">
      <ul>
        <li className="back">
          <Link to="/vwemailers">
            <div className="thumb"> <img src={volkswagenEmailerImg} alt="" /> </div>
            <div className="button"><span>Back</span></div>
          </Link>
        </li>
        <li className="next">
          <Link to="/flyerdesign">
            <div className="thumb"> <img src={radioShowImg} alt="" /> </div>
            <div className="button"><span>next</span></div>
          </Link>
        </li>
      </ul>
    </div>

  </div>
)

export default ebayindia



